
.content-list-wrap{background: #f6f6f6;font-size: 1em;
}
.content-list{margin-top: 3.25rem;background: #f6f6f6;overflow: hidden;
}
.item-picture{padding-bottom: 1.5625rem
}
.content-list .item-text{background: #fff;margin:0 0 0.5em 0;-webkit-box-shadow: 0 0.0625em 0 0 rgba(230, 230, 230, 0.5);box-shadow: 0 0.0625em 0 0 rgba(230, 230, 230, 0.5)
}
.content-list p{margin:0 1.25rem;
}
.item-picture-date{color: #000;opacity: 0.6;font-size: 1.25rem;line-height: 1.375rem;text-align: center;padding-top: 1rem;font-family: 'Courier New';
}
.content-list .issue-no{margin-top: 0.25rem;margin-bottom: 0;
}
.item-picture-img{width: 100%;margin: 0.625rem 0 0.9375rem;
}
.picture-content{color: 000;opacity: 0.6;font-size: 0.875rem;line-height: 1.625rem;margin: 1.25rem 0 1.125rem;
}



